<!-- eslint-disable no-return-assign -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :categoryId.sync="filterCategoryId" :colorId.sync="filterColor" />
      <section class="catalog">
        <ProductList
        :products="products"
        :productLoad="productsLoading"
        :productLoadFailed="productsLoadingFailed"
        @load-products="loadProducts()">
        </ProductList>
        <BasePanagation v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>
<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePanagation from '@/components/BasePanagation.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePanagation, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      page: 1,
      productsPerPage: 9,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    // filteredProducts() {
    //   let filterProducts = products;
    //   if (this.filterPriceFrom > 0) {
    //     filterProducts = filterProducts.filter(
    // (product) => product.price > this.filterPriceFrom);
    //   }

    //   if (this.filterPriceTo > 0) {
    //     filterProducts = filterProducts.filter((product) => product.price < this.filterPriceTo);
    //   }

    //   if (this.filterCategoryId) {
    //     // eslint-disable-next-line max-len
    //     filterProducts = filterProducts.filter(
    //       (product) => product.categoryId === this.filterCategoryId,
    //     );
    //   }

    //   if (this.filterColor) {
    //     filterProducts = filterProducts.filter(
    //       (product) => product.colorsId.includes(this.filterColor),
    //     );
    //   }
    //   return filterProducts;
    // },
    products() {
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
      const res = this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
          colorsId: product.colors.map((color) => color.code),
        }))
        : [];
      console.log(res);
      return res;
    },
    countProducts() {
      // return this.filteredProducts.length;
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            categoryId: this.filterCategoryId,
            colorId: this.filterColor,
          },
        })
          .then((response) => {
          // handle success
            this.productsData = response.data;
            console.log(this.productsData);
          })
          .catch((error) => {
            this.productsLoadingFailed = true;
            console.log(error);
          })
          .then(() => { this.productsLoading = false; })
          .finally(() => {
          // always executed
          });
      }, 500);
      // axios.get('https://vue-study.skillbox.cc/api/products')
      //   .then((response) => this.productsData = response.data);
    },
  },
  created() {
    this.loadProducts();
  },
};

</script>
