import BaseFormField from '@/components/BaseFormField.vue';

export default {
  props: ['placeholder', 'error', 'placeholderCenter', 'value', 'nameInput'],
  components: { BaseFormField },
  computed: {
    dataValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit('input', val);
      },
    },
  },
};
