<template>
  <div class="product__counter form__counter">
    <button type="button" aria-label="Убрать один товар"
    @click.prevent="updateCartProductAmount( { productIds: item.productId, count: count - 1 })">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <label for="count">
      <input type="text" v-model.number="count" name="count" id="count">
    </label>
    <button type="button" aria-label="Добавить один товар"
    @click.prevent="updateCartProductAmount(
      { productIds: item.productId, count: count + 1 }
      )">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  props: ['item'],

  computed: {
    count: {
      get() {
        return this.item.count;
      },
      set(value) {
        this.updateCartProductAmount({ productIds: this.item.productId, count: value });
      },
    },
  },
  methods: {
    ...mapActions(['updateCartProductAmount', 'incProducttoCart', 'decProducttoCart']),
    // deleteProductFromCart(productId) {
    //   this.$store.commit('deleteProductfromCart', productId);
    // },
  },
};
</script>
