<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li v-for="item in productCalc" :key="item.productId" class="cart__order">
        <h3>
          {{ item.products.title }}
        </h3>
        <b>
          {{(item.products.price * item.count) | numberFormat}} ₽
        </b>
        <span>
          Артикул: {{ item.productId }}
        </span>
</li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ totalProductCount }}</b> товара на сумму <b>
        {{ totalPrice | numberFormat }}₽</b></p>
    </div>

    <button class="cart__button button button--primery" type="submit" :disabled="cartSend">
      Оформить заказ
    </button>
    <p v-if="cartSend">Оформление заказа...</p>
    <p v-else-if="cartSendFailed">при оформлении возникли ошибки...</p>
    <p v-else-if="cartSending">Заказ оформлен</p>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  props: ['cartSend', 'cartSendFailed', 'cartSending'],
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ productCalc: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalProductCount: 'cartTotalCountProduct' }),
  },
};
</script>
