<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ totalProductCount }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
            type="text"
            placeholder="ФИО"
            :error="formError.name"
            placeholderCenter="Введите ваше полное имя"
            v-model="formData.name"
            nameInput="name"/>

            <BaseFormText
            type="text"
            placeholder="Адрес доставки"
            :error="formError.address"
            placeholderCenter="Введите ваш адрес"
            v-model="formData.address"
            nameInput="address"/>

            <BaseFormText
            type="tel"
            placeholder="Телефон"
            :error="formError.phone"
            placeholderCenter="Введите ваш телефон"
            v-model="formData.phone"
            nameInput="phone"/>

            <BaseFormText
            type="email"
            placeholder="Email"
            :error="formError.email"
            placeholderCenter="Email"
            v-model="formData.email"
            nameInput="email"/>

            <BaseFormTextAria
            placeholder="Комментарий к заказу"
            :error="formError.comment"
            placeholderCenter="Ваши пожелания"
            v-model="formData.comment"
            nameInput="comment"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="delivery">
                  <input class="options__radio sr-only" type="radio"
                  id="delivery" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="delivery2">
                  <input class="options__radio sr-only"
                  type="radio" id="delivery2" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="pay">
                  <input class="options__radio sr-only" type="radio"
                  id="pay" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="pay2">
                  <input class="options__radio sr-only" type="radio"
                  id="pay2" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
<OrderStructure :cartSend="cartSend" :cartSendFailed="cartSendFailed" :cartSending="cartSending"/>

        <div class="cart__error form__error-block" v-if="formErrorMessange">

          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessange }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextAria from '@/components/BaseFormTextAria.vue';
import OrderStructure from '@/components/OrderStructure.vue';
import axios from 'axios';
import {
  API_BASE_URL,
} from '@/config';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessange: '',
      cartSend: false,
      cartSendFailed: false,
      cartSending: false,
    };
  },
  components: { BaseFormText, BaseFormTextAria, OrderStructure },
  computed: {
    ...mapGetters({ totalProductCount: 'cartTotalCountProduct' }),
    ...mapMutations({ reset: 'resetCart' }),
  },
  methods: {
    order() {
      this.cartSend = true;
      this.cartSendFailed = false;
      this.cartSending = false;
      this.formError = {};
      this.formErrorMessange = '';
      return (new Promise((resolve) => {
        setTimeout(resolve, 500);
      }))
        .then(() => axios.post(`${API_BASE_URL}api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        }))
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          // this.reset();
          this.cartSend = false;
          this.cartSendFailed = false;
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((er) => {
          this.cartSend = false;
          this.cartSendFailed = true;
          // console.log(er.response);
          this.formError = er.response.data.error.request || {};
          this.formErrorMessange = er.response.data.error.message;

          // console.log(er);
        })
        .then(() => {
          this.cartSending = true;
        });
    },
  },
};
</script>
